* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.img {
  height: 40px;
}

.navbar__links {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navbar__option {
  text-align: left;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.navbar__option:last-child {
  margin-right: 0;
}

.navbar__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.navbar__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
